import React, { Component, Fragment } from 'react';

import './CreateAccountPage.css';
import Header from '../../components/Header/Header';
import { Link } from 'react-router-dom';
import { api, CONSTANTS } from '../../services/api.service';
import { pubSub } from '../../services/pubsub.service';
import { Redirect } from 'react-router';


export default class CreateAccountPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isVerificationVisible: false,
            isVerificationPopVisible: false,
            email: null,
            validEmail: true,
            toAccount: false
        };
    }

    submitForVerification = () => {
        pubSub.publish('pageloader', {
            show: true
        });
        let fcmToken = localStorage.getItem('fcm-token');
        if  (fcmToken) {
            try {
                fcmToken = JSON.parse(fcmToken);
            } catch(e) {
                //swallow
            }
        }
        const email = document.querySelector('[name="email"]').value; 
        const password = document.querySelector('[name="password"]').value;
        if (email && password) {
            const dataObj = {
                "email": email,
                "token": fcmToken,
                "status": "IN_PROGRESS",
                "password": password,
                "preferences": {
                     
                },
                "tokens": [fcmToken]
            };
            api.post({
                url: CONSTANTS.createAccount, 
                data: dataObj
            }).then(resp => {
                 api.post({
                     url: CONSTANTS.getOTP,
                     data: {
                        token: fcmToken
                    }
                 });
                this.setState({
                    email: email,
                    isVerificationVisible: true,
                    isVerificationPopVisible: false
                });
    
                pubSub.publish('pageloader', {
                    show: false
                });
            }).catch(() => {
                pubSub.publish('pageloader', {
                    show: false
                });
            });
        } else {
            pubSub.publish('pageloader', {
                show: false
            });
        }
    }

    getOTP() {
        let fcmToken = localStorage.getItem('fcm-token');
        if  (fcmToken) {
            try {
                fcmToken = JSON.parse(fcmToken);
            } catch(e) {
                //swallow
            }
        }
        api.post({
            url: CONSTANTS.getOTP,
            data: {
               token: fcmToken
           }
        });
    }
    showSuccessVerification = () => {
        this.setState({
            isVerificationVisible: false,
            isVerificationPopVisible: true
        });
    }

    handleChange = (e) => {
        if (e && e.target) {
            this.setState({
                [e.target.name]: e.target.value
            });
        }
    }
    validateEmail = (e) => {
        api.post({
            url: CONSTANTS.validateEmail,
            data: {
                email: e.target.value
            }
        }).then(resp => {
            if (resp.success) {
                this.setState({
                    validEmail: true
                })
            } else {
                this.setState({
                    validEmail: false
                });
            }
        });
    }
    goToDashboard = (e) => {
        pubSub.publish('pageloader', {
            show: true
        });
        api.get({
            url: CONSTANTS.getAccount + '?email=' + this.state.email
        }).then(resp => {
            if (resp.success) {
                localStorage.setItem("userData", JSON.stringify(resp.data));
                localStorage.setItem('authToken', resp.token);
                localStorage.setItem('authType', 'LOCAL');
                this.setState({
                    toAccount: true
                });
            } else {

            }
            pubSub.publish('pageloader', {
                show: false
            });
        }).catch(() => {
            pubSub.publish('pageloader', {
                show: false
            });
        })
    }

    render() {
        if (this.state.toAccount) {
            return (<Redirect to="/my-application"/>);
        }
        return (
            <Fragment>
                <Header />
                <div className="create-account-section">
                    {
                        !this.state.isVerificationVisible && !this.state.isVerificationPopVisible ? (
                            <Fragment>
                                <h2 className="margin-head"> CREATE YOUR ACCOUNT </h2>
                                <div className="bg-white margin-head form-text-color">
                                    <div className="margin-head">
                                        <div className={'m-2-bottom form-error ' + (this.state.validEmail ? 'hidden': '')}>Email is already exists</div>
                                        <form>
                                            <label htmlFor="email">Email</label>
                                            <input type="email" id="email" name="email" onBlur={this.validateEmail} onChange={this.handleChange} />
                                            <label htmlFor="phone">Phone</label>
                                            <input type="number" id="phone" name="phone" onChange={this.handleChange} />
                                            <div className="pass-border">
                                                <label htmlFor="password" className="margin-pass">Password</label>
                                                <input type="password" id="password" name="password" onChange={this.handleChange} />
                                            </div>
                                            <div className="bg-white"></div>
                                            <div className="pass-border">
                                                <label htmlFor="pwd">Confirm Password</label>
                                                <input type="password" id="passwordconfirm" name="passwordconfirm" onChange={this.handleChange} />
                                            </div>
                                        </form>
                                    </div>
                                    <div className="margin-head paragraph-color">
                                        <p> We need to vaidate your identity.</p>
                                        <p> How would you like to receive an authentcation code? </p>
                                    </div>
                                    <div className="check-text">
                                        <input type="checkbox" id="totext" defaultChecked value="TEXT"/>
                                        <label htmlFor="totext" className="check-style">Text</label>
                                        <input type="checkbox" id="toemail" value="EMAIL"/>
                                        <label htmlFor="toemail">Email</label>
                                    </div>
                                </div>
                                <div>
                                    <button type="button" disabled={this.state.validEmail ?  false : true } onClick={this.submitForVerification} className="btn-primary button-style">Next</button>
                                </div>
                            </Fragment>
                        ) : null
                    }
                    {
                        this.state.isVerificationVisible && !this.state.isVerificationPopVisible ? (
                            <Fragment>
                                <h2 className="margin-head"> VERIFY YOUR IDENTITY </h2>
                                <div className="bg-white margin-head form-text-color">
                                    <p> We sent a text-message to your mobile at xxx-xxx-7890 with a code to enter below. </p>
                                    <div>
                                        <p>Enter Verification Code</p>
                                        <div className="verify-box">
                                            <input type="text" maxLength="1" size="1" min="0" max="9" pattern="[0-9]{1}" />
                                            <input type="text" maxLength="1" size="1" min="0" max="9" pattern="[0-9]{1}" />
                                            <input type="text" maxLength="1" size="1" min="0" max="9" pattern="[0-9]{1}" />
                                            <input type="text" maxLength="1" size="1" min="0" max="9" pattern="[0-9]{1}" />
                                            <input type="text" maxLength="1" size="1" min="0" max="9" pattern="[0-9]{1}" />
                                            <input type="text" maxLength="1" size="1" min="0" max="9" pattern="[0-9]{1}" />
                                        </div>
                                    </div>
                                </div>
                                <div>
                                    <button type="button" onClick={this.showSuccessVerification} className="btn-primary button-style">Submit</button>
                                    <a href="#" onClick={(e) => { e.preventDefault(); this.getOTP();}} className="anchor-style">Resend?</a>
                                </div>
                            </Fragment>
                        ) : null
                    }
                    {
                        !this.state.isVerificationVisible && this.state.isVerificationPopVisible ? (
                            <Fragment>
                                <div className="pop-up">
                                    <div>
                                        <i className="fa fa-thumbs-o-up" aria-hidden="true"></i>
                                    </div>
                                    <h2>Good Job!</h2>
                                    <p>You have successfully created your account.<br />You can update your password <br />under your Profile.</p>
                                    <a href="#" className="btn-primary" onClick={(e) => {e.preventDefault();this.goToDashboard();}}> Go to Dashboard </a>
                                    <a href="#" className="close-btn">&#10005;</a>
                                </div>
                            </Fragment>
                        ) : null
                    }
                </div>
            </Fragment>
        )
    }
}



