import React, { Component } from 'react';
import OktaAuth from '@okta/okta-auth-js';

import './Login.css';
import { AuthContext } from '../../adalConfig';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router';
import { pubSub } from '../../services/pubsub.service';



export default class Login extends Component {
    constructor(props) {
        super(props);
        let redirectUrl = `${location.origin}/oktacallback`;
        this.oktaAuth = new OktaAuth({
            url: 'https://dev-508930.oktapreview.com',
            clientId: '0oapb5pupu4AFzyjJ0h7',
            redirectUri: redirectUrl,
            tokenManager: {
                storage: 'localStorage'
            }
        });
        this.state = {
            toAccount: false,
            toDashboard: false
        };
    }
    loginWithAzure() {
        AuthContext.login();
    }
    loginWithOkta() {
        this.oktaAuth.token.getWithRedirect({
            responseType: 'id_token'
        });
    }
    loginWithLocal = async () => {
        pubSub.publish('pageloader', {
            show: true
        });
        let email = document.querySelector('[name="email"]').value;
        let password = document.querySelector('[name="password"]').value;
        if (email && password) {
            let baseURL = 'https://us-central1-cap-pwa.cloudfunctions.net';
            if (location.href.indexOf('localhost') >= 0) {
                baseURL = 'http://localhost:5001/cap-pwa/us-central1';
            }
            let fcmToken = localStorage.getItem('fcm-token');
            if  (fcmToken) {
                try {
                    fcmToken = JSON.parse(fcmToken);
                } catch(e) {
                    //swallow
                }
            }
            const authenticateResponse = await fetch(baseURL + '/authenticate?version=' + localStorage.getItem('version'), {
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json'
                },
                credentials: 'include',
                body: JSON.stringify({
                    email, password,
                    token: fcmToken
                })
            });
            const json = await authenticateResponse.json();
            if (json.success) {
                localStorage.setItem("userData", JSON.stringify(json.data));
                localStorage.setItem('authToken', json.token);
                localStorage.setItem('authType', 'LOCAL');
                let foundRedirection = false;
                if  (json.data.status === 'IN_PROGRESS') {
                    this.setState({
                        toAccount: true
                    });
                    foundRedirection = true;
                }
                if  (json.data.status === 'SUBMITTED' || json.data.status === 'APPROVED' || json.data.status === 'UNDER_REVIEW') {
                    this.setState({
                        toDashboard: true
                    });
                    foundRedirection = true;
                }

                if (!foundRedirection) {
                    this.setState({
                        toAccount: true
                    });
                }
            }
            pubSub.publish('pageloader', {
                show: false
            });
        } else {
            pubSub.publish('pageloader', {
                show: false
            });
        }
    }
    render() {
        if (this.state.toAccount) {
            return (<Redirect to="/my-application"/>);
        }
        if (this.state.toDashboard) {
            return (<Redirect to="/dashboard"/>);
        }
        return (
            <div>
                <div className="login-container m-2-left m-2-right">
                    <h3>Welcome to STRAYER UNIVERSITY</h3>
                    <form className="form-body">
                        <div className="form-field">
                            <p className="form-field-para text-left">Username</p>
                            <input className="input" type="text" name="email"/>
                        </div>
                        <div className="form-field">
                            <p className="form-field-para text-left">Password</p>
                            <input className="input" type="password" name="password"/>
                        </div>
                        <div className="flex items-center">
                            <button className="btn-primary" type="button" onClick={(e) => this.loginWithLocal()}>Login</button>
                            <Link to="/create-account">Create Account</Link>
                        </div>
                    </form>
                    <div className="flex items-center justify-between">
                        <button className="btn-primary flex-1" type="button" onClick={(e) => this.loginWithAzure()}>Login With Azure</button>
                        <button className="btn-primary flex-1 margin-left-10" type="button" onClick={(e) => this.loginWithOkta()}>Login With Okta</button>
                    </div>
                </div>
            </div>
        )
    }
}



