import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { api, CONSTANTS } from '../../services/api.service';
import { pubSub } from '../../services/pubsub.service';

export default class AuthCallbackPage extends Component {
    constructor(props) {
        super(props);
        const token = location.hash.replace('#id_token=', '');
        localStorage.setItem('authToken', token);
        localStorage.setItem('authType', props.logintype);
        let fcmToken = localStorage.getItem('fcm-token');
        if  (fcmToken) {
            try {
                fcmToken = JSON.parse(fcmToken);
            } catch(e) {
                //swallow
            }
        }
        this.state = {
            loginSuccess: false,
            token,
            toApplication: false,
            toDashboard: false
        };
        if (props.logintype ===  'OKTA') {
            if (fcmToken) {
                api.post({
                    url: CONSTANTS.authenticateOkta,
                    method: 'POST',
                    data: {
                        authToken: token,
                        token: fcmToken
                    }
                }).then((resp) => {
                    localStorage.setItem("userData", JSON.stringify(resp.data));
                    let userData = resp.data;
                    if (userData instanceof Array) {
                        userData = userData[0];
                    }
                    if  (userData.status && (userData.status === 'UNDER_REVIEW' || userData.status === 'APPROVED')) {
                        this.setState({
                            toDashboard: true
                        });
                    }  else {
                        this.setState({
                            toApplication: true
                        });
                    }
                    
                });
            } else {
                window.onFCMLoad = (function(args) {
                    this.getFCMToken();
                }).bind(this);
            }
            
        } else {
            this.state.loginSuccess = true;
        }
    }

    getFCMToken () {
        let fcmToken= null;
        try {
            fcmToken = JSON.parse(localStorage.getItem('fcm-token'));
        } catch(e) {
            //swallow
        }
        api.post({
            url: CONSTANTS.authenticateOkta,
            method: 'POST',
            data: {
                authToken: this.state.token,
                token: fcmToken
            }
        }).then((resp) => {
            localStorage.setItem("userData", JSON.stringify(resp.data));
            let userData = resp.data;
            if (userData instanceof Array) {
                userData = userData[0];
            }
            if  (userData.status && (userData.status === 'UNDER_REVIEW' || userData.status === 'APPROVED')) {
                this.setState({
                    toDashboard: true
                });
            }  else {
                this.setState({
                    toApplication: true
                });
            }
            this.setState({
                loginSuccess: true
            });
        });
    }

    render() {
        if (this.state.loginSuccess) {
            return (<Redirect to="/"/>)
        }
        if (this.state.toDashboard) {
            return (<Redirect to="/"/>)
        }
        if (this.state.toApplication) {
            return (<Redirect to="/my-application"/>)
        }
        
        return (
            <div>
                Waiting for Login
            </div>
        );
    }
}


