let baseURL = 'https://us-central1-cap-pwa.cloudfunctions.net';
if (location.href.indexOf('localhost') >= 0) {
    baseURL = 'http://localhost:5001/cap-pwa/us-central1'
}
export const CONSTANTS = {
    baseURL: baseURL,
    createAccount: '/accounts',
    getOTP: '/getOTP',
    authenticate: '/authenticate',
    validateEmail: '/validateEmail',
    updateAccount: '/accounts',
    getAccount: '/accounts',
    authenticateOkta: '/authenticateOkta',
    authenticateAzure: '/authenticateAzure'
}

export const api = {
    api: async function(url, data, method, opts) {
        let params = {
            data: data || {},
            method: method,
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        };
        try {
            let fetchURL = CONSTANTS.baseURL + (url.indexOf('/') >= 0 ? url : '/' + url);
            fetchURL = fetchURL + (fetchURL.indexOf('?') >= 0 ? '&' : '?') + 'version=' + localStorage.getItem('version');
            return await fetch(fetchURL, params).then(resp => {
                if(opts && opts.isHTML) {
                    return resp.text()
                } else {
                    return resp.json()
                }
            });
        }
        catch (e) {}
    },
    post: function(opts){
        return this.api(opts.url, opts.data, 'POST', opts);
    },
    get: function(opts){
        return this.api(opts.url, opts.data, 'GET', opts);
    }
}


