import React, { Component } from 'react';
import { Route, HashRouter } from 'react-router-dom';
import LoginPage from './pages/LoginPage/LoginPage';
import AuthCallbackPage from './pages/AuthCallbackPage/AuthCallbackPage';
import CreateAccountPage from './pages/CreateAccountPage/CreateAccountPage';
import DashboardPage from './pages/DashboardPage/DashboardPage';
import MyApplication from './pages/MyApplication/MyApplication';
import { pubSub } from './services/pubsub.service';

export default class Layout extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: false
        };
        pubSub.subscribe('pageloader', (event) => {
            if (event.show) {
                this.setState({
                    isLoading: true
                });
            } else {
                this.setState({
                    isLoading: false
                });
            }
        });
    }
    render() {
        // let AddToHomeTag = (<div></div>);
        // if (!window.navigator['standalone'] && !window.matchMedia('(display-mode: standalone)').matches) {
        //     const userAgent = window.navigator.userAgent.toLowerCase();
        //     const isIphone = /iphone|ipad|ipod/.test(userAgent) ? true : false;
        //     let addToHomeScreenContent;
        //     if (isIphone) {
        //         addToHomeScreenContent = ("Install this webapp on your iPhone: tab and then Add to hoemscreen");
        //     } else {
        //         addToHomeScreenContent = ("Install this webapp on your phone: Install option in address bar");
        //     }
        //     AddToHomeTag = (
        //         <div>
        //             <div className="add-to-homescreen-prompt">
        //                 <div className="add-to-homescreen-prompt__content">
        //                     {addToHomeScreenContent}
        //                 </div>
        //                 <div className="add-to-homescreen-prompt__tip"></div>
        //             </div>
        //         </div>
        //     );
        // }
        return (
            <div>
                <Route path="/" exact={true} component={DashboardPage} />
                <Route path="/dashboard" exact={true} component={DashboardPage} />
                <Route path="/my-application" exact={true} component={MyApplication} />
                <Route path="/create-account" exact={true} component={CreateAccountPage} />
                <Route path="/login" exact={true} component={LoginPage} />
                <Route path="/azurecallback" component={() => <AuthCallbackPage logintype="AZURE"/>}  />
                <Route path="/oktacallback" component={() => <AuthCallbackPage logintype="OKTA"/>}  />
                {/* {AddToHomeTag} */}
                <div className={'page-loader ' + (this.state.isLoading ? '': 'hidden')}>
                    <div className="page-loader__overlay"></div>
                    <div className="page-loader__content">
                        <div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
                    </div>
                </div>
            </div>
        )
    }
}


