import React, { Component, Fragment } from 'react';
import './Header.css';
import { Link } from 'react-router-dom';
import { Redirect,  } from 'react-router';

export default class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isMenuNavVisible: false,
            toLogin: false
        };
    }

    showMenuNav = (e) => {
        e.preventDefault();
        this.setState({
            isMenuNavVisible: true
        });
    }

    hideMenuNav = (e) => {
        e.preventDefault();
        this.setState({
            isMenuNavVisible: false
        });
    }

    logout() {
        localStorage.clear();
        this.setState({
            toLogin: true
        });
    }

    render() {
        let logoutHTML;
        let isLoginPage = false;
        if (location.href.indexOf('/login') < 0 || location.href.indexOf('/create-account') < 0) {
            isLoginPage = true;
            logoutHTML = (<li>
                <a href="/" onClick={(e) => { e.preventDefault();this.logout(); }}>Logout</a>
            </li>);
        }
        if (this.state.toLogin) {
            return (<Redirect to="/login"/>);
        }
        return (
            <div>
                <div className="cmp-header">
                    <a href="#" onClick={this.showMenuNav} className="cmp-header__menu-icon">
                        <i className="fa fa-bars"></i>
                    </a>
                    <p className="cmp-header__logo">STRAYER</p>
                </div>
                <div className={ this.state.isMenuNavVisible ? 'cmp-header__nav-menu show-menu' : 'cmp-header__nav-menu' }>
                    <a href="#" onClick={this.hideMenuNav} className="cmp-header__menu-icon">
                        <i className="fa fa-arrow-left"></i>
                    </a>
                    <p className="cmp-header__logo">STRAYER</p>
                    <ul>
                        {
                            !isLoginPage ? (
                                <li>
                                    <Link to="/">About Us</Link>
                                </li>
                            ) : (
                                <Fragment>
                                    <li>
                                        <Link to="/"> Dashboard </Link>
                                    </li>
                                    <li>
                                        <Link to="/">Financial Aid</Link>
                                    </li>
                                    <li>
                                        <Link to="/">Academic PLan</Link>
                                    </li>
                                    <li>
                                        <Link to="/">Calender</Link>
                                    </li>
                                </Fragment>
                            )
                        }
                        {logoutHTML}
                    </ul>
                </div>
                <div className={ this.state.isMenuNavVisible ? 'page-overlay show-overlay' : 'page-overlay' }></div>
            </div>
        )
    }
}



