import React, { Component } from 'react';
import Header from '../../components/Header/Header';
import Login from '../../components/Login/Login';
import { Redirect } from 'react-router';
export default class LoginPagge extends Component {
    constructor(props)  {
        super(props);
        this.state = {
            toMyApplication: false
        };
        if (localStorage.getItem('authToken') && localStorage.getItem('userData')){
            this.state.toMyApplication = true;
        }
    }
    render() {
        if (this.state.toMyApplication) {
            return (<Redirect to="/"/>);
        }
        return (
            <div>
                <Header></Header>
                <Login></Login>
            </div>
        )
    }
}


